<template>
  <div>
    <form id="sign-in" v-on:submit.prevent="signIn">
        <input type="text" id="username" name="username" v-model="username" placeholder="Username"> 
        <br/> 
        <input type="text" id="password" name="password" v-model="password" placeholder="Password"> 
        <br/> 
        <input type="submit" value="Sign In"/>
        <p class="error">{{error}}</p>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import {eventBus} from "../main";


export default {
  name: "SignInForm",
  props: [],
  data() {
    return {
        username: "", 
        password: "", 
        error: "", 
    };
  },
  created: function() {
  },
  methods: {
      signIn() {
        axios.post("/api/user/signin", {username: this.username, password: this.password})
        .then((response) => {  
            eventBus.$emit("signin-success", {
              data: response.data.username
            })
        })
        .catch((error) => {
            this.error = this.error = error.response.data.error; 
        }).finally(() => {
          eventBus.$emit("freet-data-update"); 
        })
        this.username = "";
        this.password = "";
    } 
  },
};
</script>

