<template>
  <div>
    <form id="create-user" v-on:submit.prevent="createUser">
        <input type="text" id="username" name="username" v-model="username" placeholder="Username"> 
        <br/> 
        <input type="text" id="password" name="password" v-model="password" placeholder="Password"> 
        <br/> 
        <input type="submit" value="Create User"/>
        <p class="error">{{error}}</p>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import {eventBus} from "../main";

export default {
  name: "CreateUserForm",
  props: [],
  data() {
    return {
        username: "", 
        password: "", 
        error: "", 
    };
  },
  created: function() {
  },
  methods: {
      createUser() {
        axios.post("/api/user", {username: this.username, password: this.password})
        .then((response) => {
            eventBus.$emit('signin-success', {data: response.data.username});
            this.error = ""
            eventBus.$emit("freet-data-update"); 
        })
        .catch((error) => {
            this.error = error.response.data.error;
        })
        this.username = ""
        this.password = ""
    }
  },
};
</script>

